import 'reflect-metadata';
import express = require('express');
import { routes } from './src/routes/index';

const app = express();
const { PORT = 3000 } = process.env;

app.use(express.json());

app.use(routes);

app.listen(PORT, () => {
  console.log(`[server]: Server is running at http://localhost:${PORT}`);
});
